export default class Producto {
    nombre: string;
    precio: number;
    img: string;

    constructor(nombre, precio, img) {
        this.nombre = nombre;
        this.precio = precio;
        this.img = img;
    }

    mostrarProducto() {
        let prod: string;
        prod = this.nombre + ' ' + Number(this.precio) + '€<br />';
        return prod;

    }
}



