import { Component } from '@angular/core';
import Producto from './producto';
import Pedido from './pedido';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  nombres: String[] = ['Ensalada', 'Croquetas', 'Bacalao', 'Hamburguesa', 'Cerveza', 'Vino'];
  precios: Number[] = [9, 7, 19, 10, 3, 3];
  imagenes: String[] = ['1.jpg', '2.jpg', '3.jpg', '4.jpg', '5.jpg', '6.jpg'];
  lista: Producto[] = [];
  pedido: Pedido = new Pedido();

  constructor() {
    for (let i = 0; i < this.nombres.length; i++) {
      this.lista[i] = new Producto(this.nombres[i], this.precios[i], this.imagenes[i]);
    }
  }

  handleOnEnviar($event) {
    this.pedido.agregarProducto($event);
  }
}



